	module.exports = {"data":{"authors":{"edges":[{"node":{"id":"eddie","name":"Eddie Lee","image":"/images/eddielee.jpg","url":"https://twitter.com/eddielee6","bio":"Father and Software Engineer"}}]}},"pathContext":{"nodes":[{"node":{"frontmatter":{"title":"Good Fucking Design Advice","tags":["design","quotes","quote post"],"cover":"https://unsplash.it/1152/300/?random?Design","date":"2014-01-25T12:43","author":"eddie"},"fields":{"slug":"/design-advice"},"html":"<p><img src=\"/images/design-advice-work-harder.jpg\" alt=\"Work fucking harder.\"></p>\n<p><img src=\"/images/design-advice-love-your-job.jpg\" alt=\"Love your fucking job.\"></p>\n<p><img src=\"/images/design-advice-do-it-right.jpg\" alt=\"Do it right the first fucking time.\"></p>\n<p><img src=\"/images/design-advice-criticism.jpg\" alt=\"Learn to take some fucking criticism.\"></p>\n<p><img src=\"/images/design-advice-challange-yourself.jpg\" alt=\"Constantly fucking challenge yourself.\"></p>\n<p><img src=\"/images/design-advice-helvetica.jpg\" alt=\"Use fucking Helvetica.\"></p>\n<p><img src=\"/images/design-advice-get-over-yourself.jpg\" alt=\"Get over your fucking self.\"></p>\n<p><img src=\"/images/design-advice-promote.jpg\" alt=\"Promote your fucking self.\"></p>\n<p><img src=\"/images/design-advice-give-a-damn.jpg\" alt=\"Give a fucking damn.\"></p>\n<p><img src=\"/images/design-advice-never-assume.jpg\" alt=\"Never assume fucking anything.\"></p>\n<p><small>Source <a href=\"http://goodfuckingdesignadvice.com\">goodfuckingdesignadvice.com</a></small></p>","timeToRead":1}},{"node":{"frontmatter":{"title":"Had a great birthday with my son","tags":["family","personal"],"cover":"https://unsplash.it/1152/300/?random?Tigger","date":"2013-10-20T22:36","author":"eddie"},"fields":{"slug":"/had-a-great-birthday-with-my-son"},"html":"<p><img src=\"/images/eddie-ethan-tigger.jpg\" alt=\"Ethan and me\"></p>","timeToRead":1}},{"node":{"frontmatter":{"title":"Homerpalooza","tags":["The Simpsons","quotes"],"cover":"https://unsplash.it/1152/300/?random?SmashingPumpkins","date":"2013-09-12T21:20","author":"eddie"},"fields":{"slug":"/homerpalooza"},"html":"<p>What a great episode</p>\n<p><img src=\"/images/homerpalooza-1.png\" alt=\"Billy Corgan, Smashing Pumpkins.\"></p>\n<p><img src=\"/images/homerpalooza-2.png\" alt=\"Homer Simpson, smiling politely.\"></p>\n<p><small>Source The Simpsons S07E24</small></p>","timeToRead":1}},{"node":{"frontmatter":{"title":"What's it like to be a dad?","tags":["family","personal"],"cover":"https://unsplash.it/1152/300/?random?Dad","date":"2013-08-20T08:01","author":"eddie"},"fields":{"slug":"/whats-it-like-to-be-a-dad"},"html":"<p>Ever since Ethan was born people have been asking me questions along the lines of, <em>\"what does it feel like to be a dad?\"</em>.</p>\n<p>Well, you know when you have a birthday, and someone says <em>\"how's it feel to be 18?\"</em>, or <em>\"how's it feel to be another year older?\"</em>.</p>\n<p>Yeah? Well it feels like that.</p>","timeToRead":1}},{"node":{"frontmatter":{"title":"We Give a F*** How the Site Loads","tags":["code","quote post"],"cover":"https://unsplash.it/1152/300/?random?FuckCss","date":"2013-08-13T22:57","author":"eddie"},"fields":{"slug":"/we-give-a-f-how-the-site-loads"},"html":"<p>Looks like some people have been having difficulty with deviantART recently. The site has been loading, but not the CSS.</p>\n<p>What was the issue? Well here's the bug report...</p>\n<blockquote>\n<p>I just wanted to let you know that the reason why deviantART's CSS isn't loading properly for some people is because one of your CSS files has f*** in a stylesheet comment.</p>\n</blockquote>\n<p>Yes, a comment in the CSS that included the word \"fuck\" was being picked up by over overzealous filtering systems, resulting in the file being blocked, and not loaded.</p>\n<p>What was the fix? Well besides removing the comment from the file, deviantART have now moved to use <a href=\"http://lesscss.org\">LESS</a>, a CSS preprocessor.</p>\n<p>This isn't a blog post to talk about the benefits of CSS preprocessors, but as deviantART inform us they cannot stop their developers from swearing in comments, this should stop these developer comments from appearing in served css files again.</p>\n<p><small>Source <a href=\"http://dt.deviantart.com/journal/We-Give-a-F-How-the-Site-Loads-392679726\">deviantART</a></small></p>","timeToRead":1}}],"page":8,"pages":9,"total":42,"limit":5,"prev":"/7","next":"/9"}}

